  font-size: 2.7rem;
}

.section-header h3 {
  gont-size: 2.2rem;
}

.section-header::before {
  content: "";
  position: absolute;
  width: 140px;
  bottom: 15px;
  height: 1px;
  background: #ebebeb;
}

.section-header::after {
  content: "";
  position: absolute;
  width: 24px;
  height: 24px;
  bottom: 4px;
  border: 5px solid #fff;
  border-radius: 20px;
  background: #2977af;
}

/** Шапка **/
.header {
  background: #fafafa;
}

.header__wrapper {
  background: url("../images/header.jpg") 50% 65% no-repeat;
  clip-path: polygon(50% 0%, 100% 0, 100% 91%, 50% 100%, 0 91%, 0% 60%, 0 0);
  height: 980px;
  background-size: cover;
  position: relative;
}

/** Меню **/
.header__black-i12 {
  position: absolute;
  right: 100px;
  bottom: 125px;
  width: 200px;
  transition: all .4s ease;
}

.header__black-i12:hover {
  transform: translateY(-10px);
}

.menu {
  transition: all .5s ease;
  padding: 10px 0 5px 0;
  position: fixed;
  left: 0;
  right: 0;
  background: rgba(20, 20, 20, 0.85);
  z-index: 1000;
}

.menu.out {
  transform: translateY(-100%);
}

.menu__nav {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.1rem;
}

.menu__nav-wrapper {
  list-style: none;
  display: flex;
  margin: 0;
}

.menu__elem {
  display: flex;
  align-items: center;
  position: relative;
  padding: 0 10px;
}

.menu__elem:not(:last-child)::after {
  content: "";
  width: 1px;
  height: 35px;
  background: rgba(255, 255, 255, 0.44);
  display: block;
  position: absolute;
  right: 0;
  top: 7px;
}

.menu__elem a {
  color: rgba(255, 255, 255, 0.66);
  display: block;
  padding: 10px 10px 20px;
  transition: background-position-x 1s ease, color 1s ease;
}

.menu__elem a:hover {
  background: url("data:image/svg+xml,%3Csvg width='500' height='50' xmlns='http://www.w3.org/2000/svg'%3E%3Cg%3E%3Ctitle%3Ebackground%3C/title%3E%3Crect fill='none' id='canvas_background' height='52' width='502' y='-1' x='-1'/%3E%3C/g%3E%3Cg%3E%3Ctitle%3ELayer 1%3C/title%3E%3Cpath id='svg_1' d='m0,47.585c0,0 97.5,0 130,0c13.75,0 28.74,-38.778 46.168,-19.416c16.501,18.331 67.435,19.416 83.832,19.416c31.821,0 130,0 130,0' stroke-miterlimit='10' stroke-width='1.5' stroke='%23ffffff' fill='none'/%3E%3Cpath id='svg_3' d='m372.058572,30.483178' opacity='0.5' stroke-width='1.5' fill='%232179d6'/%3E%3Cline stroke-linecap='undefined' stroke-linejoin='undefined' id='svg_6' y2='48.130199' x2='500.629723' y1='47.710032' x1='389.705593' stroke-width='1.5' stroke='%23ffffff' fill='none'/%3E%3C/g%3E%3C/svg%3E") 80% 100% repeat-x;
  animation: line 1s;
  background-size: 300% 60%;
  color: #fff;
}

.menu__elem.active a {
  color: #fff;
}

.menu__phone {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 15px;
  color: #dbdbdb;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
}

.menu__phone-number {
  color: #ffffff !important;
  padding: 0;
}

.menu__phone-number:hover {
  color: #d7f3ff !important;
}

@keyframes line {
  0% {
    background-position-x: -17%;
  }
}

/** Верхняя секция **/
.header__top {
  padding: 150px 0 0 0;
}

.header__top-i12 {
  text-align: center;
  font-size: 3.3rem !important;
  font-weight: 700;
}

.header__top-i12desc {
  text-align: center;
  font-size: 2.5rem !important;
  font-weight: 600;
}

.header__top-i12desc2 {
  font-size: 1.9em !important;
  margin-top: 40px;
  font-weight: 600;
}

.header__advantages {
  list-style: none;
  font-size: 1.4rem;
  font-weight: 600;
  margin: 60px 0 0 auto;
  width: 390px;
}

.header__advantages-item {
  padding: 15px 0 0 35px;
  background-image: url("../images/check.svg");
  background-repeat: no-repeat;
  background-position: 0 70%;
  background-size: 25px;
}

.header__buy {
  padding: 15px 50px;
  background: rgba(27, 99, 160, 0.87);
  border: 1px solid transparent;
  border-radius: 40px;
  font-size: 1.5rem;
  color: #ffffff;
  transition: all ease .4s;
}

.header__buy:hover {
  background: rgba(255, 255, 255, 0.18);
  border: 1px solid rgba(85, 85, 85, 0.7);
  color: #262626;
}

.header__form {
  box-shadow: 2px 2px 5px 1px rgba(0, 0, 0, 0.21);
  padding: 25px;
  border-radius: 3px 25px 3px;
  display: inline-block;
  margin: 0 0 10px;
  text-align: center;
}

.header__mobile-hint {
  font-size: 1.3rem;
}

.header__mobile-number {
  display: block;
  margin: 0 auto 25px;
  width: 200px;
  font-size: 1.3rem;
  border: none;
  padding: 15px 0;
  border-bottom: solid 1px #002645;
  transition: all 0.3s cubic-bezier(0.64, 0.09, 0.08, 1);
  background-size: 200px 100%;
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 96%, #002645 4%) no-repeat -200px 0;
  color: black;
}

.header__mobile-number:focus, .header__mobile-number:valid {
  box-shadow: none;
  outline: none;
  background-position: 0 0;
}

.header__required {
  color: red;
}

/** Почему мы **/
.why-we {
  background: #fafafa;
}

.why-we__item {
  border-bottom: 1px solid #e2e2e2;
  padding: 4rem 0 2rem 0;
}

.why-we__header::after {
  border: 5px solid #fafafa;
}

.why-we__item-content {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: flex-start;
}

.why-we__item-img {
  width: 150px;
}

.why-we__item-img.suppliers {
  width: 200px;
}

.why-we__item-img.truck {
  width: 250px;
}

.why-we__item-img.communication {
  width: 220px;
}

.why-we__item-header {
  font-size: 1.8rem;
  font-weight: 600;
  border-bottom: 1px solid #e2e2e2;
  padding: 0 0 10px;
}

.why-we__item-text {
  font-size: 1.2rem;
  font-weight: 400;
}

/** Характеристики **/
.characters {
  background-size: cover;
  background-image: url("../images/char-back.jpg");
  background-position: 50% 50%;
  color: #fff;
  padding: 0 0 4rem;
  position: relative;
}

.characters__i12tws {
  position: absolute;
  top: 23rem;
  left: 50%;
  width: 270px;
  margin-left: -135px;
}

.characters__container {
  padding: 0 40px;
}

.characters__header {
  padding: 6rem 10px 7rem;
}

.characters__header::before {
  bottom: 64px;
  background: #2f2f2f;
}

.characters__header::after {
  bottom: 56px;
  width: 16px;
  height: 16px;
  border: none;
  background: #929292;
}

.characters__item-wrapper {
  flex: 0 1 70%;
}

.characters__item {
  border-left: 1px solid #2f2f2f;
  border-top: 1px solid #2f2f2f;
