}

.characters__item-text {
  font-size: 1.2rem;
  color: #c6c6c6;
}

.characters__buy-wrapper {
  display: flex;
  justify-content: center;
  margin: 3rem 0 0 0;
}

.characters__buy {
  margin: 15px 0 0 0;
  padding: 22px 70px;
  background: rgba(27, 99, 160, 0.87);
  border-radius: 40px;
  font-size: 1.7rem;
  color: #ffffff;
  transition: all ease .4s;
  text-align: center;
  border: none;
}

.characters__buy:hover {
  background: #e7e7e7;
  color: #262626;
}

/** Timeline **/
.timeline {
  background: #fafafa;
}

.timeline__header::after {
  border: 5px solid #fafafa;
}

.timeline__content {
  margin: 4rem 0 0 0;
  position: relative;
}

.timeline__content::before {
  position: absolute;
  top: 0;
  bottom: 105px;
  left: 50%;
  width: 2px;
  transform: translateX(-50%);
  content: "";
  background-color: #e0e0e0;
  z-index: 1;
}

.timeline__item {
  position: relative;
  min-height: 280px;
  display: flex;
  z-index: 10;
}

.timeline__item-content {
  flex: 0 1 35%;
  padding: 0 0 0 7%;
  text-align: right;
}

.timeline__item--inverted {
  justify-content: flex-end;
}

.timeline__item--inverted .timeline__item-content {
  padding: 0 7% 0 0;
  text-align: left;
}

.timeline__item-img {
  width: 200px;
  height: 200px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  overflow: hidden;
}

.timeline__item-img img {
  width: 100%;
  border: 7px solid #e9ecef;
  border-radius: 100%;
}

.timeline__item-header {
  font-size: 1.7rem;
  font-weight: 600;
  line-height: 1.3;
}

.timeline__item-text {
  font-size: 1.2rem;
  padding: .5rem 0 0 0;
  color: #404040;
}

/** Подарок **/
.gift__header {
  margin: 0 0 4rem 0;
}

.gift__text {
  font-size: 1.5rem;
  text-align: center;
  margin: 5rem 0;
}

.gift__gift-name {
  color: #0065ce;
}

.gift__slider {
  width: 90%;
  margin: 0 auto;
}

.gift__slider-img {
  width: 100%;
  max-height: 700px;
}

.gift__dots {
  position: absolute;
  bottom: -45px;
  display: block;
  width: 100%;
  padding: 0;
  list-style: none;
  text-align: center;
}

.gift__dots li {
  position: relative;
  display: inline-block;
  width: 20px;
  height: 20px;
  margin: 0 5px;
  padding: 0;
  cursor: pointer;
}

.gift__dots li button {
  font-size: 0;
  line-height: 0;
  display: block;
  width: 20px;
  height: 20px;
  padding: 5px;
  cursor: pointer;
  color: transparent;
  border: 0;
  outline: none;
  background: transparent;
}

.gift__dots li button:hover,
.gift__dots li button:focus {
  outline: none;
}

.gift__dots li button:hover::before,
.gift__dots li button:focus::before {
  opacity: 1;
}

.gift__dots li button::before {
  font-size: 6px;
  line-height: 20px;
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  content: url("../images/gift/circle.svg");
  text-align: center;
  opacity: .25;
  color: black;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.gift__dots li.slick-active button:before {
  opacity: .75;
  color: black;
}

.gift__arrow {
  width: 30px;
  height: 30px;
  cursor: pointer;
  display: inline-block;
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  opacity: .7;
}

.gift__arrow:hover {
  opacity: 1;
}

.gift__arrow.next {
  background-image: url("../images/gift/next.svg");
  background-size: 100% 100%;
  right: -35px;
}

.gift__arrow.prev {
  background-image: url("../images/gift/prev.svg");
  background-size: 100% 100%;
  left: -35px;
}

